.page-family {
  position: relative;
  .center {
    width: 100%;
    transform: translateY(-.3rem);
    .report-download {
      position: absolute;
      left: 11.5rem;
      top: 1.05rem;
      font-size: .18rem;
      a {
        display: inline-block;
        color: #00fff9;
        padding-bottom: .01rem;
        border-bottom: .015rem solid #00fff9;
      }
    }
  }
  .left {
    position: absolute;
    left: .5rem;
    top: 0;
    width: 3.8rem;

  }
  .right {
    position: absolute;
    right: .5rem;
    top: 0;
    width: 5.3rem;
  }
}

.c-room-view {
  height: 10rem;
  width: 100%;
  transform: translateX(-0.8rem);
  background: url("./assets/room-bg.png") no-repeat center top;
  background-size: 14.45rem 9.95rem;
}

.c-device-model {
  position: absolute;
  overflow: visible;
  .highlight {
    position: absolute;
    pointer-events: none;
  }
  .name {
    position: absolute;
    font-size: .14rem;
    color: #ffa608;
    white-space: nowrap;
    pointer-events: none;
  }
}

.c-device-model-popup {
  min-width: 1.15rem;
  background:rgba(10,44,91,0.56);
  border:1px solid rgba(0,160,233,1);
  .title {
    height: 0.125rem;
    padding: 0 0.03rem;
    font-size: 0.07rem;
    line-height: 0.125rem;
    font-weight: bold;
    background:linear-gradient(90deg,rgba(0,141,212,1) 0%,rgba(0,145,217,0) 100%);
  }
  .content {
    padding: 0.075rem;
    font-size: 0.08rem;
    line-height: 2;
    white-space: nowrap;
  }
}

.c-device-model-ant-tooltip.ant-tooltip {
  padding: 0;
  .ant-tooltip-arrow {
    display: none;
  }
  .ant-tooltip-inner {
    background: none;
    box-shadow: none;
    padding: 0;
  }
}

.c-device-auth {
  width: 8.6rem;
  .title {
    font-size: .2rem;
    font-weight: 600;
    color: #00fff9;
    margin-bottom: .2rem;
    i {
      display: inline-block;
      width: .19rem;
      height: .19rem;
      vertical-align: bottom;
      background: url("./assets/i_question.png") no-repeat;
      background-size: 100% 100%;
    }
  }
  .devices {
    display: flex;
    justify-content: space-between;
    padding-left: .3rem;
  }
  .device {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    &.checked .icon {
      opacity: 1;
    }
    &.checked::after {
      content: "";
      display: block;
      position: absolute;
      width: .18rem;
      height: .18rem;
      left: 50%;
      top: .6rem;
      transform: translateX(-50%);
      background: url("./assets/i_tick.png") no-repeat;
      background-size: 100% 100%;
    }
  }
  .icon {
    opacity: .3;
    margin-bottom: .2rem;
  }
}

.c-event-list {
  >.content {
    margin-left: .12rem;
    padding: 0;
    height: 2.4rem;
    li {
      height: .6rem;
      padding: 0 .1rem;
      font-size: .16rem;
      display: flex;
      align-items: center;
    }
  }
  .label {
    padding: 0.01rem 0.12rem;
    background:rgba(252,100,45,0.16);
    border-radius:7px;
    border:1px solid #f8e71c;
    color: #f8e71c;
    font-size: .18rem;
    margin-right: .05rem;
  }
  em {
    font-style: normal;
    color: #FFA608;
  }
  li.alarm {
    background:rgba(255,26,59,0.3);
    .label {
      background:rgba(252,100,45,0.16);
      border:1px solid #fc642d;
      color: #fc642d;
    }
  }
  .pager {
    text-align: right;
    color: #00adff;
    font-weight: 500;
    user-select: none;
    line-height: 1.5;
    .btn {
      cursor: pointer;
    }
  }
}

.c-chart-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .item {
    width: 2.4rem;
    margin-bottom: .1rem;
  }
  .chart {
    height: 1.3rem;
    margin-bottom: .05rem;
  }
  .name {
    text-align: center;
  }
}

.c-family-select {
  position: absolute;
  left: 4.5rem;
  top: 1rem;
  display: flex;
  align-items: center;
  >.ant-select {
    width: 2rem;
    margin-right: .45rem;
    >.ant-select-selection {
      height: .32rem;
      background: #02274a;
      border-radius:0.02rem;
      border:1px solid rgba(0,255,249,0.77);
      color: #ffffff;
      font-size: .14rem;
      >div {
        margin-top: .07rem;
        padding-left: .03rem;
      }
      .ant-select-arrow {
        color: #ffffff;
        transform: scale(2) translateX(-0.05rem);
      }
    }
  }
}

.c-family-select-dropdown.ant-select-dropdown {
  width: 200px !important;
  background: #02274a;
  border:1px solid rgba(0,255,249,0.77);
  li {
    color: #fff;
  }
  .ant-select-dropdown-menu-item-selected {
    background: rgba(0,255,249,0.2);
  }
  .ant-select-dropdown-menu-item-active {
    background: rgba(0,255,249,0.5);
  }
}